@charset "UTF-8";
/*ГЛОБАЛЬНЫЕ СТИЛИ*/
@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-regular.woff2") format("woff2"), url("../font/poppins-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../font/poppins-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../font/poppins-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorantgaramond-italic.woff2") format("woff2"), url("../font/cormorantgaramond-italic.woff") format("woff");
  font-weight: 400;
  font-style: italic;
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorantgaramond-mediumitalic.woff2") format("woff2"), url("../font/cormorantgaramond-mediumitalic.woff") format("woff");
  font-weight: 500;
  font-style: italic;
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorantgaramond-semibold.woff2") format("woff2"), url("../font/cormorantgaramond-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorantgaramond-bold.woff2") format("woff2"), url("../font/cormorantgaramond-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  height: 100%; }

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/*БЛОКИ*/
.site-pages {
  width: 290px;
  margin: 0 auto;
  padding: 50px 15px; }

.site-pages h1 {
  margin: 0 auto;
  margin-bottom: 20px;
  font-family: "Cormorant Garamond", "Times New Roman", serif;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  color: #1d1613; }

.site-pages ul {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  counter-reset: list; }

.site-pages li {
  margin-bottom: 10px;
  font-family: "Poppins", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: #1d1613; }

.site-pages li::before {
  padding-right: 5px;
  content: counter(list) ".";
  counter-increment: list; }

.site-pages a {
  color: #1d1613; }
